<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- 设备组名称 -->
            <a-form-model-item
              label="设备组名称"
              prop="resourceGroupName"
              ref="resourceGroupName"
            >
              <a-input
                v-model="entity.resourceGroupName"
                placeholder="请输入设备组名称"
                name="resourceGroupName"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 所属设备家族 -->
            <a-form-model-item
              label="所属设备家族"
              prop="resourceFamilyId"
              ref="resourceFamilyId"
            >
              <a-select
                v-model="entity.resourceFamilyId"
                placeholder="请选择所属设备家族"
                name="resourceFamilyId"
              >
                <a-select-option v-for="item in familyList" :key="item.id" :value="item.id">
                  {{ item.name }}
                </a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 设备组类型 -->
            <a-form-model-item
              label="设备组类型"
              prop="resourceGroupType"
              ref="resourceGroupType"
            >
              <a-select
                v-model="entity.resourceGroupType"
                placeholder="请选择设备组类型"
                name="resourceGroupType"
              >
                <a-select-option value="生产">生产</a-select-option>
                <a-select-option value="测试">测试</a-select-option>
                <a-select-option value="开发">开发</a-select-option>
                <a-select-option value="其他">其他</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 状态 -->
            <a-form-model-item
              label="状态"
              prop="status"
              ref="status"
            >
              <a-select
                v-model="entity.status"
                placeholder="请选择状态"
                name="status"
              >
                <a-select-option value="启用">启用</a-select-option>
                <a-select-option value="禁用">禁用</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="12">
            <!-- 描述信息 -->
            <a-form-model-item label="描述" prop="description">
              <a-textarea
                v-model:value="entity.description"
                placeholder="请输入描述信息"
                :rows="4"
                :max-length="1000"
                name="description"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>
      <g-collapse headerName="详细信息">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <!-- 创建人 -->
              <a-form-model-item label="创建人">
                <span>{{ entity.createdBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 创建时间 -->
              <a-form-model-item label="创建时间">
                <span>{{ entity.createdTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改人 -->
              <a-form-model-item label="修改人">
                <span>{{ entity.modifiedBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改时间 -->
              <a-form-model-item label="修改时间">
                <span>{{ entity.modifiedTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";
export default {
  name: "CreateOrEditResourceGroup",
  mixins: [GNdoPageEditBase],
  data() {
    return {
      entity: {
        resourceGroupName: "",
        resourceFamilyId: "",
        resourceGroupType: "",
        status: "启用",
        description: "",
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: ""
      },
      familyList: [], // 设备家族列表
      rules: {
        resourceGroupName: [
          { required: true, message: "设备组名称不能为空", trigger: "change" },
          {
            pattern: /^[^\s]+(\s+[^\s]+)*$/,
            trigger: "change",
            message: "名称格式不正确，不能包含前后空格",
          },
        ],
        resourceFamilyId: [
          { required: true, message: "所属设备家族不能为空", trigger: "change" }
        ],
        resourceGroupType: [
          { required: true, message: "设备组类型不能为空", trigger: "change" }
        ]
      },
    };
  },

  mounted() {
    this.getFamilyList();
  },

  methods: {
    // 获取设备家族列表
    getFamilyList() {
      // 模拟获取设备家族列表数据
      setTimeout(() => {
        this.familyList = [
          { id: 1, name: "交换机" },
          { id: 2, name: "路由器" },
          { id: 3, name: "服务器" },
          { id: 4, name: "存储设备" },
          { id: 5, name: "防火墙" }
        ];
      }, 300);
    },
    
    // 初始化
    init() {
      console.log(this.id, "初始化");
      this.loading = true;
      if (this.id) {
        // 模拟请求获取设备组数据
        setTimeout(() => {
          this.loading = false;

          this.entity = {
            resourceGroupName: "核心交换组",
            resourceFamilyId: 1,
            resourceGroupType: "生产",
            status: "启用",
            description: "核心网络交换设备分组，负责数据中心核心网络架构",
            createdBy: "admin",
            createdTime: "2025-03-18 10:30",
            modifiedBy: "admin",
            modifiedTime: "2025-03-18 11:55"
          };
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },
    
    resetFormData() {
      // 重置表单绑定的数据源
      this.entity = {
        resourceGroupName: "",
        resourceFamilyId: "",
        resourceGroupType: "",
        status: "启用",
        description: "",
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: ""
      };
    },
    
    async save() {
      // 保存 校验
      let res = await this.checkError();
      if (res) {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.resourceGroupName);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null,
          });
        }, 500);
      }
    },
    
    inChange(e){
      console.log(e.target.value);
    }
  },
};
</script>

<style lang="scss" scoped></style> 